html {
	font-size: 18px;
  font-family: 'Source Code Pro', monospace;
}



form {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	margin: 2rem 0;
}


.content-wrap {
	width: 90%;
	max-width: 900px;
	height: initial;
	min-height: 60vh;
	margin: auto;
	padding: 2rem 1rem;
	overflow-x: hidden;
	outline: 1px solid #ccc;

}








